"use client"

import React, { useEffect, useState } from 'react'
import { useParams } from 'next/navigation'
import { API_URL } from '@/lib/api'
import Header from "@/components/header"
import Footer from "@/components/footer"
import { useAuth } from '@/lib/auth-context'

// Interface for Prompt data
interface Prompt {
  id: number
  title: string
  content: string
  owner_id: number
  created_at: string
  likes: number
  dislikes: number
}

export default function PromptDetailPage() {
  const { id } = useParams()
  const { user } = useAuth()
  const [prompt, setPrompt] = useState<Prompt | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [voteLoading, setVoteLoading] = useState(false)

  useEffect(() => {
    if (id) {
      const fetchPrompt = async () => {
        try {
          const response = await fetch(`${API_URL}/prompts/${id}`)
          if (!response.ok) {
            if (response.status === 404) {
              throw new Error('ÌîÑÎ°¨ÌîÑÌä∏Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.')
            }
            throw new Error('ÌîÑÎ°¨ÌîÑÌä∏ Ï†ïÎ≥¥Î•º Í∞ÄÏ†∏Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
          }
          const data: Prompt = await response.json()
          setPrompt(data)
        } catch (err) {
          setError((err as Error).message)
        } finally {
          setLoading(false)
        }
      }
      fetchPrompt()
    }
  }, [id])

  const handleVote = async (voteType: 'like' | 'dislike') => {
    if (!user) {
      setError("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.")
      return;
    }
    if (voteLoading || !prompt) return;

    setVoteLoading(true)
    setError(null)
    
    const token = localStorage.getItem('accessToken');
    if (!token) {
        setError("Ïù∏Ï¶ù ÌÜ†ÌÅ∞Ïù¥ ÏóÜÏäµÎãàÎã§.");
        setVoteLoading(false);
        return;
    }

    try {
      const response = await fetch(`${API_URL}/prompts/${prompt.id}/${voteType}`, {
        method: 'POST',
        headers: {
            'Authorization': `Bearer ${token}`
        },
      });

      if (!response.ok) {
        throw new Error(`${voteType === 'like' ? 'Ï¢ãÏïÑÏöî' : 'Ïã´Ïñ¥Ïöî'} Ï≤òÎ¶¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.`);
      }

      const updatedPrompt: Prompt = await response.json();
      setPrompt(updatedPrompt); // Update the state with the new data from the server

    } catch (err) {
      setError((err as Error).message);
    } finally {
      setVoteLoading(false);
    }
  };

  return (
    <>
      <Header />
      <main className="min-h-screen bg-gray-100 py-12">
        <div className="container mx-auto p-4 max-w-4xl">
          {loading && <p className="text-center text-gray-600">ÌîÑÎ°¨ÌîÑÌä∏ Î∂àÎü¨Ïò§Îäî Ï§ë...</p>}
          {error && <p className="text-red-500 text-center">Ïò§Î•ò: {error}</p>}

          {prompt && (
            <div className="bg-white shadow-lg rounded-lg p-8">
              <h1 className="text-4xl font-extrabold text-gray-900 mb-4">{prompt.title}</h1>
              <p className="text-gray-700 text-lg whitespace-pre-wrap mb-6">{prompt.content}</p>
              
              <div className="flex justify-between items-center text-gray-500 text-sm border-t pt-4">
                <span>ÏûëÏÑ±Ïûê ID: {prompt.owner_id}</span>
                <span>ÏûëÏÑ±Ïùº: {new Date(prompt.created_at).toLocaleDateString()}</span>
                
                <div className="flex items-center space-x-4">
                  <button
                    onClick={() => handleVote('like')}
                    disabled={voteLoading}
                    className="flex items-center space-x-1 text-blue-500 hover:text-blue-700 disabled:opacity-50"
                  >
                    <span>üëç</span>
                    <span>{prompt.likes}</span>
                  </button>
                  <button
                    onClick={() => handleVote('dislike')}
                    disabled={voteLoading}
                    className="flex items-center space-x-1 text-red-500 hover:text-red-700 disabled:opacity-50"
                  >
                    <span>üëé</span>
                    <span>{prompt.dislikes}</span>
                  </button>
                </div>
              </div>
            </div>
          )}

          {!loading && !prompt && !error && (
            <p className="text-center text-gray-600">ÏÑ†ÌÉùÌïú ÌîÑÎ°¨ÌîÑÌä∏Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.</p>
          )}
        </div>
      </main>
      <Footer />
    </>
  )
}
